@import "../../bootstrap/config.bs";

#directorist.atbd_wrapper {
  padding: 18px 20px;

  .atcc_pt_40{
    padding-top: 40px;
  }

  * {
    box-sizing: border-box;
  }
  .iris-picker, .iris-picker * {
    -moz-box-sizing: content-box;
    -webkit-box-sizing: content-box;
    box-sizing: content-box;
  }
  #gmap {
    height: 350px;
  }
  label {
    margin-bottom: $label-margin-bottom;
    display: inline-block;
    font-weight: 500;
    font-size: 15px;
    color: $dark;
  }
  .map_wrapper {
    position: relative;
    #floating-panel {
      position: absolute;
      z-index: 2;
      right: 59px;
      top: 10px;
    }
  }

  a.btn {
    text-decoration: none;
  }
  [data-toggle="tooltip"]{
    color: #a1a1a7;
    font-size: 12px;
    &:hover{
      color: $dark;
    }
  }
  .single_prv_attachment{
    text-align: center;
    div{
      position: relative;
      display: inline-block;
      .remove_prev_img{
        position: absolute;
        top: -5px;
        right: -5px;
        background: #d3d1ec;
        line-height: 26px;
        width: 26px;
        border-radius: 50%;
        transition: 0.2s;
        cursor: pointer;
        color: #ffffff;
        padding: 0;
        &:hover{
          color: #c81d1d;
        }
      }
    }
  }
  #listing_image_btn span {
    vertical-align: text-bottom;
  }
  .default_img {
    margin-bottom: 10px;
    text-align: center;
    margin-top: 10px;
    small {
      color: $text-color;
      font-size: 13px;
    }
  }
  .atbd_pricing_options{
    margin-bottom: 15px;
    label{
      font-size: 13px;
    }
    .bor{
      margin: 0 15px;
    }
    small{
      font-size: 12px;
      vertical-align: top;
    }
  }
  .listing-img-container {
    text-align: center;
    padding: 10px 0 15px;
    p {
      margin-top: 15px;
      margin-bottom: 4px;
      color: #7a82a6;
      font-size: 16px;
    }
    small {
      color: $text-color;
      font-size: 13px;
    }
    .single_attachment {
      width: 208px;
      display: inline-block;
      position: relative;
      .remove_image {
        position: absolute;
        top: -5px;
        right: -5px;
        background: #d3d1ec;
        line-height: 26px;
        width: 26px;
        border-radius: 50%;
        -webkit-transition: 0.2s;
        -moz-transition: 0.2s;
        -ms-transition: 0.2s;
        -o-transition: 0.2s;
        transition: 0.2s;
        cursor: pointer;
        color: $light;
        &:hover {
          color: $danger;
        }
      }
    }
  }
  .field-options {
    margin-bottom: 15px;
  }
  .hide-if-no-js {
    text-align: center;
    margin: 0;
  }
  .form-check {
    margin-bottom: 25px;
    display: flex;
    align-items: center;
    input {
      vertical-align: top;
      margin-top: 0;
    }
    .form-check-label {
      margin: 0;
      font-size: 15px;
    }
  }
  .atbd_optional_field {
    margin-bottom: 15px;
  }
  .extension_detail{
    margin-top: 20px;
    .btn_wrapper{
      margin-top: 25px;
    }
    &.ext_d{
      min-height:140px;
      position: relative;
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      p{
        margin: 0;
      }
      .btn_wrapper{
        width: 100%;
        margin-top: auto;
      }
      & > a, p, div{
        display: block;
      }
      & > p{
        margin-bottom: 15px;
      }
    }
  }
  .ext_title {
    a{
      text-align: center;
      text-decoration: none;
      font-weight: 500;
      font-size: 18px;
      color: $dark;
      transition: 0.3s;
      display: block;
    }
    &:hover {
      a{
        color: #6e63ff;
      }
    }
    .text-center{
      text-align: center;
    }
  }
  .attc_extension_wrapper {
    margin-top: 30px;
    .col-md-4{
      .single_extension{
        .btn{
          padding: 3px 15px;
          font-size: 14px;
        }
      }
    }
    .single_extension {
      margin-bottom: 30px;
      background: #ffffff;
      box-shadow: 0px 5px 10px #e1e7f7;
      padding: 25px;
      img {
        width: 100%;
      }
      &.coming_soon{
        img{
          opacity: 0.6;
        }
        a{
          pointer-events: none !important;
        }
        .ext_title{
          a:after{
            content: '(Coming Soon)';
            color: #ff0000;
            font-size: 14px;
          }
          &:hover a{
            color: inherit;
          }
        }
        .btn{
          opacity: 0.5;
        }
      }
    }
  }
  .attc_extension_wrapper__heading{
    margin-bottom: 15px;
  }
  .btn_wrapper {
    a + a {
      margin-left: 10px;
    }
  }
  &.atbd_help_support {
    .wrap_left {
      width: 70%;
    }
    h3{
      font-size: 24px;
    }
    a{
      color: #387dff;
      &:hover{
        text-decoration: underline;
      }
    }
    .postbox {
      padding: 30px;
      h3{
        margin-bottom: 20px;
      }
    }
    .wrap {
      display: inline-block;
      vertical-align: top;
    }
    .wrap_right {
      width: 27%;
      .postbox {
        background: #0073aa;
        border-radius: 3px;
        box-shadow: 0 10px 20px rgba(103, 103, 103, 0.27);
        h3{
          color: #fff;
          margin-bottom: 25px;
        }
      }
    }
  }
  .shortcode_table td {
    font-size: 14px;
    line-height: 22px;
  }
  ul.atbdp_pro_features {
    li {
      font-size: 16px;
      margin-bottom: 12px;
      a{
        color: #ededed;
        &:hover{
          color: #fff;
        }
      }
    }
  }

  .atbdp-radio-list,
  .atbdp-checkbox-list{
    li{
      label{
        text-transform: capitalize;
        font-size: 13px;
        input{
          margin-right: 7px;
        }
      }
    }
  }
  .single_thm{
    .ext_title{
      h4{
        text-align: center;
      }
    }
    .btn_wrapper{
      text-align: center;
    }
  }
  /***********************************************************
************************************************************
css for Custom Field
*************************************************************
**************************************************************/
  .postbox table.widefat {
    box-shadow: none;
    background: #eff2f5;
  }

  #atbdp-field-details td {
    color: #555;
    font-size: 17px;
