/* csv styles */
.csv-action-btns{
  margin-top: 30px;
  a{
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    line-height: 44px;
    padding: 0 20px;
    background: #fff;
    border: 1px solid #E3E6EF;
    color: #272B41;
    border-radius: 5px;
    font-weight: 600;
    margin-right: 7px;
    span{
      color: #9299B8;
    }
    &:last-child{
      margin-right: 0;
    }
    &.btn-active{
      background: #2C99FF;
      color: #fff;
      border-color: #2C99FF;
      span{
        color: rgba(#fff, 0.8);
      }
    }
  }
}
.csv-action-steps{
  ul{
    width: 700px;
    margin: 80px auto 0;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    li{
      text-align: center;
      position: relative;
      text-align: center;
      width: 25%;
      &:before{
        position: absolute;
        content: url(../images/csv-step.png);
        left: 112px;
        top: 8px;
        width: 125px;
        overflow: hidden;
      }
      .step{
        display: inline-flex;
        align-items: center;
        justify-content: center;
        width: 34px;
        height: 34px;
        border-radius: 50%;
        color: #9299B8;
        box-shadow: 5px 0 10px rgba(#9299B8, 0.15);
        background: #fff;
        .dashicons{
          margin: 0;
          display: none;
        }
      }
      .step-text{
        display: block;
        margin-top: 15px;
        color: #9299B8;
        font-weight: 500;
      }
      &.active{
        .step{
          background: #272B41;
          color: #fff;
        }
        .step-text{
          color: #272B41;
        }
      }
      &.done{
        &:before{
          content: url(../images/csv-step-done.png);
        }
        .step{
          background: #0FB73B;
          color: #fff;
          .step-count{
            display: none;
          }
          .dashicons{
            display: block;
          }
        }
        .step-text{
          color: #272B41;
        }
      }
      &:last-child:before, &:last-child.done:before{
        content: none;
      }
    }
  }
}
